import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { AppService } from 'src/app/app.service';
import { CompromisoModel } from 'src/app/commons/models/gestion-academica/compromiso.model';
import { AuthenticationService } from 'src/app/commons/services/authentication.service';
import { CompromisoService } from 'src/app/commons/services/compromiso.service';
import { EstadoService } from 'src/app/commons/services/estado.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { setCompromisoEdit } from 'src/app/store/actions/compromiso.actions';
import { AppState } from 'src/app/store/app.reducer';


@Component({
  selector: 'app-compromiso-predocente',
  templateUrl: './compromiso-predocente.component.html',
  styles: [
  ]
})
export class CompromisoPreDocenteComponent implements OnInit {
  public _subscribeCompromiso: Subscription;
  public listaCompromisos: CompromisoModel[] = []
  public compromisoAceptado: CompromisoModel;


  constructor(
    private router: Router,
    private appService: AppService,
    public toastr: ToastrService,
    public store: Store<AppState>,
    public alertService: UtilAlert,
    public estadoService: EstadoService,
    private auth: AuthenticationService,
    private compromisoService: CompromisoService,

  ) {
    this.appService.pageTitle = 'Mis Compromisos';
  }

  ngOnInit(): void {
    this._compromisoState()
    this.traerCompromisos()
  }
  ngOnDestroy(): void {
  }

  private _compromisoState() {
    this._subscribeCompromiso = this.store.select("compromiso").subscribe(
    )
  }

  traerCompromisos() {
    this.compromisoService.getCompromisoByRut(this.auth.obtenerUserName()).subscribe((value) => {
      this.listaCompromisos = value;
      console.log(this.listaCompromisos)
    })
  }

  seleccionarCompromiso(compromiso: CompromisoModel) {
    this.store.dispatch(setCompromisoEdit({ compromiso }))
    this.router.navigate(['../compromisos/compromiso-form/view']);
  }

  aceptarCompromiso(compromiso: CompromisoModel) {
    this.compromisoService.aprobarCompromiso(compromiso).subscribe((value) => {
      this.compromisoAceptado = value;
      this.store.dispatch(setCompromisoEdit({ compromiso: this.compromisoAceptado }))
      this.router.navigate(['../compromisos/compromiso-form/view'])
    })

  }
}